<template lang="html">
  <div>
    <p v-for='(url, index) in urlList'>
      <a :href='url.url | getquery(url.name, url.age)'>{{url.url | getquery(url.name, url.age)}}</a>
    </p>
    <p v-for='tel in telephone'>{{tel | newtel}}</p>
    <ul>
      <li v-for = " (item,index) in list" v-text='`${item} - ${index} `'></li>
    </ul>
    <ul>
      <li v-for = " (item,index) in filter" v-text='`${item} - ${index} `'></li>
    </ul>
    <button @click="randomList"></button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      urlList: [
        {url: 'http://www.baidu.com', name: 'shay', age: 20},
        {url: 'http://www.google.com', name: 'shay1', age: 30}
      ],
      telephone: [10000000000, 10000000001, 10000000002, 10000000003, 10000000004, 10000000005, 10000000006, 10000000007, 10000000008, 10000000009, 10000000000],
      list: [1, 2, 3, 4]
    };
  },
  filters: {
    getquery (value, name, age) {
      if (!value) return '';
      return `${value}?name=${name}&age=${age}`;
    },
    newtel (value) {
      if (!value) return '';
      value = value.toString().substr(7, 4);
      // 7 * 
      value = '*'.repeat(7) + value;
      // 
      let endMember = value.substr(-1, 1);
      console.log(endMember % 2);
      //  % 2 01 01
      if (endMember % 2) {
        value = value + '';
      } else {
        value = value + '';
      }
      return value;
    }
  },
  //  
  // 1.methods
  computed: {
    // 
    filter () {
      return this.list.filter((item) => {
        return item % 2;
      });
    }
  },
  methods: {
    // data
    randomList () {
      this.list = this.list.map(item => {
        return item + Math.round(Math.random() * 9 + 1);
      });
    }
  }
};
</script>

<style lang="css">
</style>



