<template lang="html">
  <div>
    <h1>{{time}}s</h1>
    <h2>{{result}}</h2>
    <div>
      <p>{{team.china}}</p>
      <button @click="team.china++"></button>
      <p>{{team.korea}}</p>
      <button @click="team.korea++"></button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      time: 0,
      team: {
        china: 0,
        korea: 0
      }
    };
  },
  created () {
    let time = setInterval(() => {
      this.time++;
      if (this.time === 10) {
        clearInterval(time);
      }
    }, 1000);
  },
  computed: {
    result () {
      if (this.time < 10) {
        if (this.team.china > this.team.korea) {
          return '';
        } else if (this.team.china < this.team.korea) {
          return '';
        } else {
          return '';
        }
      } else {
        if (this.team.china > this.team.korea) {
          return '';
        } else if (this.team.china < this.team.korea) {
          return '';
        } else {
          return '';
        }
      }
    }
  }
};
</script>

<style lang="css">
</style>



