<template lang='html'>
  <div style='padding:20px;' id='app'>
    <div class='panel panel-primary'>
      <div class='panel-heading'></div>
      <table class='table table-bordered table-striped text-center'>
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <template v-for='(row, index) in rows'>
            <tr v-if='index >= (curpage-1) * pagesize && index < curpage * pagesize'>
              <td>{{row.Name}}</td>
              <td>{{row.Age}}</td>
              <td>{{row.School}}</td>
              <td>{{row.Remark}}</td>
            </tr>
          </template>
        </tbody>
      </table>
    </div>
    <ul class='pagination pagination-lg'>
      <template v-for='page in Math.ceil(rows.length/pagesize)'>
        <li v-on:click='PrePage()' id='prepage' v-if='page==1' class='disabled'><a href='#'></a></li>
        <li v-if='page==1' class='active' v-on:click='NumPage(page, $event)'><a href='#'>{{page}}</a></li>
        <li v-else v-on:click='NumPage(page, $event)'><a href='#'>{{page}}</a></li>
        <li id='nextpage' v-on:click='NextPage()' v-if='page==Math.ceil(rows.length/pagesize)'><a href='#'></a></li>
      </template>
    </ul>
  </div>
</template>

<script>
import $ from 'jquery';

export default {
  data () {
    return {
      rows: [
        { Id: 1, Name: '', Age: 18, School: '', Remark: '' },
        { Id: 2, Name: '', Age: 20, School: '', Remark: '' },
        { Id: 3, Name: '', Age: 19, School: '', Remark: '' },
        { Id: 4, Name: '', Age: 25, School: '', Remark: '' },
        { Id: 5, Name: '', Age: 22, School: '', Remark: '' },
        { Id: 1, Name: '', Age: 18, School: '', Remark: '' },
        { Id: 2, Name: '', Age: 20, School: '', Remark: '' },
        { Id: 3, Name: '', Age: 19, School: '', Remark: '' },
        { Id: 4, Name: '', Age: 25, School: '', Remark: '' },
        { Id: 5, Name: '', Age: 22, School: '', Remark: '' },
        { Id: 1, Name: '', Age: 18, School: '', Remark: '' },
        { Id: 2, Name: '', Age: 20, School: '', Remark: '' },
        { Id: 3, Name: '', Age: 19, School: '', Remark: '' },
        { Id: 4, Name: '', Age: 25, School: '', Remark: '' },
        { Id: 5, Name: '', Age: 22, School: '', Remark: '' },
        { Id: 1, Name: '', Age: 18, School: '', Remark: '' },
        { Id: 2, Name: '', Age: 20, School: '', Remark: '' },
        { Id: 3, Name: '', Age: 19, School: '', Remark: '' },
        { Id: 4, Name: '', Age: 25, School: '', Remark: '' },
        { Id: 5, Name: '', Age: 22, School: '', Remark: '' }
      ],
      pagesize: 6, // 
      curpage: 1 // 
    };
  },
  methods: {
    // 
    PrePage: function (event) {
      // activeNumPage
      $('.pagination .active').prev().trigger('click');
    },
    // 
    NextPage: function (event) {
      // activeNumPage
      $('.pagination .active').next().trigger('click');
    },
    // 
    NumPage: function (num, event) {
      //  === 
      if (this.curpage === num) {
        return;
      };
      this.curpage = num;
      $('.pagination li').removeClass('active');
      // liactive
      if (event.target.tagName.toUpperCase() === 'LI') {
        $(event.target).addClass('active');
      } else {
        $(event.target).parent().addClass('active');
      };
      // 
      if (this.curpage === 1) {
        $('#prepage').addClass('disabled');
      } else {
        $('#prepage').removeClass('disabled');
      };
      // 
      if (this.curpage === Math.ceil(this.rows.length / this.pagesize)) {
        $('#nextpage').addClass('disabled');
      } else {
        $('#nextpage').removeClass('disabled');
      };
    }
  }
};
</script>

<style lang='css'>
  .pagination{
    overflow: hidden;
    border: 1px solid #ccc;
  }
  .pagination li {
    float: left;
    list-style: none;
    padding: 5px 10px;
    border-right: 1px solid #ccc;
  }
  .pagination li a{
    color: #337AB7;
  }
  .pagination li.active{
    background: #337AB7;
  }
  .pagination .active a{
    color: #fff;
  }
</style>



