<template lang="html">
  <div class="">
    importtokenglobal_.token{{token}}<br>
    vueuserSitethis.GLOBAL.userSite{{userSite}}<br>
    <button @click="clickGlobal">(import)</button>
    <button @click="clickGlobalThis">(vue)</button><br>
    <button @click="changeId">id()</button><br>
    id = {{id}}<br>
  </div>
</template>

<script>
import global_ from '@/components/globalVar/globalVar'; // 
export default {
  data () {
    return {
      token: global_.token, // data
      userSite: this.GLOBAL.userSite,
      id: global_.id,
      firstName: 'Foo',
      lastName: 'Bar'
    };
  },
  methods: {
    clickGlobal () {
      global_.globalAlert();
    },
    clickGlobalThis () {
      this.GLOBAL.globalAlert();
    },
    changeId () {
      let n = Math.floor(Math.random() * 10 + 1);
      global_.id = n;
      console.log(global_.id);
      console.log(this.id);
      return n;
    }
  }
};
</script>

<style lang="css">
</style>



