<template lang="html">
  <div class="curd1">
    <input type="text" placeholder="search" @input="search" list="cars" class="search">
    <datalist id="cars">
      <option v-for="item in searchlist" :value="item"></option>
    </datalist>
    <button type="button" name="button" @click="addList"></button>
    <table>
      <thead>
        <tr>
          <th>id</th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-cloak v-for="(item, index) of slist">
            <td>{{index + 1}}</td>
            <td>{{item.username}}</td>
            <td>{{item.email}}</td>
            <td>{{item.sex}}</td>
            <td>{{item.province}}</td>
            <td><span v-for="(hobby, index) of item.hobby">{{hobby.name}}|</span></td>
            <td><a href="javascript:;" @click="editList(index)"></a> | <a href="javascript:;" @click="delectList(item.username)"></a></td>
        </tr>
      </tbody>
    </table>
    <div class="overlay" v-show="showModal"></div>
    <div class="test-modal" v-show="showModal">
      <h2>{{modalTitle}}</h2>
      <table>
        <tbody>
          <tr>
            <td></td>
            <td><input type="text" v-model="newList.username"></td>
          </tr>
          <tr>
            <td></td>
            <td><input type="text" v-model="newList.email"></td>
          </tr>
          <tr>
            <td></td>
            <td>
              <input type="radio" id="male" value="male" v-model="newList.sex">
              <label for="male"></label>
              <input type="radio" id="female" value="female" v-model="newList.sex">
              <label for="female"></label>
              <input type="radio" id="secrecy" value="secrecy" v-model="newList.sex">
              <label for="secrecy"></label>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <select v-model="newList.province">
                <option disabled value=""></option>
                <option></option>
                <option></option>
                <option></option>
                <option></option>
              </select>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <template v-for="hobby in listHobby">
                <input type="checkbox" :id="hobby.value" :value="hobby.value" v-model="newList.hobby">
                <label :for="hobby.value">{{hobby.name}}</label>
              </template>
            </td>
          </tr>
        </tbody>
      </table>
      <button type="button" name="button" @click="closeModal"></button>
      <button type="button" name="button" @click="saveList"></button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      list: [
        {
          username: 'aaaaa',
          email: '123@qq.com',
          sex: 'male',
          province: '',
          hobby: [
            {
              value: 'basketball',
              name: ''
            },
            {
              value: 'read',
              name: ''
            },
            {
              value: 'IDE',
              name: ''
            }
          ]
        },
        {
          username: 'bbbbb',
          email: 'bbbbbbb@163.com',
          sex: 'female',
          province: '',
          hobby: [{
            value: 'basketball',
            name: ''
          },
          {
            value: 'read',
            name: ''
          }]
        }
      ],
      listHobby: [
        {
          value: 'basketball',
          name: ''
        },
        {
          value: 'read',
          name: ''
        },
        {
          value: 'IDE',
          name: ''
        },
        {
          value: 'draw',
          name: ''
        },
        {
          value: 'play',
          name: ''
        }
      ],
      showModal: false,
      modalTitle: '',
      editIndex: '',
      newList: {
        username: '',
        email: '',
        sex: '',
        province: '',
        hobby: []
      },
      slist: [],
      searchlist: []
    };
  },
  mounted () {
    this.setSlist(this.list);
  },
  watch: {
    list (val, oldval) {
      this.setSlist(val);
    }
  },
  methods: {
    setSlist (arr) {
      this.slist = JSON.parse(JSON.stringify(arr));
    },
    // 
    search (e) {
      var v = e.target.value; // input
      var self = this;
      self.searchlist = []; // 
      if (v) {
        var ss = []; // 
        // 
        this.list.forEach(function (item) {
          // 
          if (item.username.indexOf(v) > -1) { // 
            if (self.searchlist.indexOf(item.username) === -1) { // 
              self.searchlist.push(item.username); // push
            }
            ss.push(item);
          } else if (item.email.indexOf(v) > -1) {
            // 
            if (self.searchlist.indexOf(item.email) === -1) {
              self.searchlist.push(item.email);
            }
            ss.push(item);
          }
        });
        this.setSlist(ss); // slist
      } else {
        // 
        this.setSlist(this.list);
      }
    },
    // 
    addList () {
      this.editIndex = ''; // 
      this.modalTitle = ''; // 
      this.showModal = true; //  true
      this.newList = { // input
        username: '',
        email: '',
        sex: 'male',
        province: '',
        hobby: []
      };
    },
    // 
    editList (n) {
      this.modalTitle = ''; // 
      this.editIndex = n; // 
      console.log(this.editIndex);
      // this.newList = this.list[this.editIndex];
      this.newList = JSON.parse(JSON.stringify(this.list[this.editIndex])); // 
      let checked = [];
      this.list[this.editIndex].hobby.forEach(function (item) {
        checked.push(item.value); // 
      });
      this.newList.hobby = checked;
      this.showModal = true;
    },
    // 
    saveList () {
      let obj = JSON.parse(JSON.stringify(this.newList)); // 
      // objhobbyvaluename
      // listHobbyhobbyname
      let checkValue = obj.hobby;
      let checked = [];
      for (var i = 0; i < checkValue.length; i++) {
        let name;
        for (var j = 0; j < this.listHobby.length; j++) {
          let value = this.listHobby[j].value;
          if (value.indexOf(checkValue[i]) > -1) {
            name = this.listHobby[j].name; // hobbyname
            break;
          };
        };
        let a = { // 
          value: checkValue[i],
          name: name
        };
        checked.push(a);
      }
      obj.hobby = checked;
      if (this.editIndex !== '') {
        // listwatch
        this.$set(this.list, this.editIndex, obj);
        // this.list[this.editIndex] = obj;
        // this.setSlist(this.list);
      } else {
        this.list.push(obj);
      }
      this.showModal = false;
    },
    // 
    delectList (n) {
      let index;
      for (var i = 0; i < this.list.length; i++) {
        if (n.indexOf(this.list[i].username) > -1) {
          console.log(i);
          index = i;
        }
      }
      this.list.splice(index, 1);
    },
    // 
    closeModal () {
      this.showModal = false;
    }
  }
};
</script>

<style lang="css" scoped>
  table{
    border-collapse: collapse;
    margin-bottom: 20px;
  }
  th,td{
    padding: 5px;
    border: 1px solid #ccc;
  }
  .overlay{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1500;
    width: 100%;
    height: 100%;
    opacity: 0.5;
    background: #000;
  }
  .test-modal{
    position: fixed;
    left: 50%;
    top: 50%;
    margin-top: -170px;
    margin-left: -210px;
    z-index: 1800;
    width: 420px;
    min-height: 340px;
    background: #fff;
    overflow: hidden;
    padding: 20px;
    box-sizing: border-box;
  }
</style>



