<template lang="html">
  <div class="">
    <p>{{ message }}</p>
    <button type="button" name="button" @click="clickNum">message</button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      message: ' F12',
      n: 0
    };
  },
  methods: {
    clickNum () {
      this.message = this.message + this.n;
      this.n++;
    }
  },
  beforeCreate () {
    console.group('beforeCreate ===============');
    console.log('%c%s', 'color:red', 'el     : ' + this.$el); // undefined
    console.log('%c%s', 'color:red', 'data   : ' + this.$data); // undefined
    console.log('%c%s', 'color:red', 'message: ' + this.message);
  },
  created () {
    console.group('created ===============');
    console.log('%c%s', 'color:red', 'el     : ' + this.$el); // undefined
    console.log('%c%s', 'color:red', 'data   : ' + this.$data); // 
    console.log('%c%s', 'color:red', 'message: ' + this.message); // 
  },
  beforeMount () {
    console.group('beforeMount ===============');
    console.log('%c%s', 'color:red', 'el     : ' + (this.$el)); // 
    console.log(this.$el);
    console.log('%c%s', 'color:red', 'data   : ' + this.$data); // 
    console.log('%c%s', 'color:red', 'message: ' + this.message); // 
  },
  mounted () {
    console.group('mounted ===============');
    console.log('%c%s', 'color:red', 'el     : ' + this.$el); // 
    console.log(this.$el);
    console.log('%c%s', 'color:red', 'data   : ' + this.$data); // 
    console.log('%c%s', 'color:red', 'message: ' + this.message); // 
  },
  beforeUpdate () {
    console.group('beforeUpdate ===============');
    console.log('%c%s', 'color:red', 'el     : ' + this.$el);
    console.log(this.$el);
    console.log('%c%s', 'color:red', 'data   : ' + this.$data);
    console.log('%c%s', 'color:red', 'message: ' + this.message);
  },
  updated () {
    console.group('updated ===============');
    console.log('%c%s', 'color:red', 'el     : ' + this.$el);
    console.log(this.$el);
    console.log('%c%s', 'color:red', 'data   : ' + this.$data);
    console.log('%c%s', 'color:red', 'message: ' + this.message);
  },
  beforeDestroy () {
    console.group('beforeDestroy ===============');
    console.log('%c%s', 'color:red', 'el     : ' + this.$el);
    console.log(this.$el);
    console.log('%c%s', 'color:red', 'data   : ' + this.$data);
    console.log('%c%s', 'color:red', 'message: ' + this.message);
  },
  destroyed () {
    console.group('destroyed ===============');
    console.log('%c%s', 'color:red', 'el     : ' + this.$el);
    console.log(this.$el);
    console.log('%c%s', 'color:red', 'data   : ' + this.$data);
    console.log('%c%s', 'color:red', 'message: ' + this.message);
  }
};
</script>

<style lang="css">
</style>



