<template lang="html">
  <div id="login" ref="userInfo">
    <a class="login" @click="show = !show"></a>
    <transition name="info">
      <div v-if="show" class="user-info">
        <div class="user-head"></div>
        <div class="user-name">astonmartin</div>
        <div class="my-channel"></div>
        <div class="logout"></div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  data () {
    return {
      show: false
    };
  },
  mounted () {
    let _this = this;
    document.addEventListener('click', function (e) {
      // console.log(e.target);
      // console.log(!_this.$refs.userInfo.contains(e.target));
      // .contains 
      if (!_this.$refs.userInfo.contains(e.target)) {
        _this.show = false;
      } else {
        _this.show = true;
      };
    });
  }
};
</script>

<style lang="css">
/*
 *v-enter: 
 *v-enter-active:  transition/animation 
 *v-leave: 
 *v-leave-active:  transition/animation 
 */
a{
  cursor: pointer;
}
.info-enter,.info-leave-active { /**/
  opacity: 0;
  transform: translate3d(0,20%,0);
}
.info-enter-active,.info-leave-active {
  transition: all .5s cubic-bezier(0.4, 0, 0, 1.5);
}

</style>



