<template lang="html">
  <div style="padding:20px;" id="app">
    <div class="panel panel-primary">
      <div class="panel-heading"></div>
      <table class="table table-bordered table-striped text-center">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <template v-for="(row, index) in rows ">
            <tr><td>{{row.Name}}</td><td>{{row.Age}}</td><td>{{row.School}}</td><td>{{row.Remark}}</td>
            <!-- <td><a href="#" @click="Edit(row, index)"></a>&nbsp;&nbsp;<a href="#" @click="DeleteId(row.Id)"></a></td> -->
            <td><a href="#" @click="Edit(row, index)"></a>&nbsp;&nbsp;<a href="#" @click="DeleteIndex(index)"></a></td>
            </tr>
          </template>
          <tr>
            <td><input type="text" class="form-control" v-model="rowtemplate.Name" /></td>
            <td><input type="text" class="form-control" v-model="rowtemplate.Age" /></td>
            <td>
              <select class="form-control" v-model="rowtemplate.School">
                <option></option>
                <option></option>
                <option></option>
              </select>
            </td>
            <td><input type="text" class="form-control" v-model="rowtemplate.Remark" /></td>
            <td><button type="button" class="btn btn-primary" v-on:click="Save"></button></td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      rows: [
        { Id: 1, Name: '', Age: 18, School: '', Remark: '' },
        { Id: 2, Name: '', Age: 20, School: '', Remark: '' },
        { Id: 3, Name: '', Age: 19, School: '', Remark: '' },
        { Id: 4, Name: '', Age: 25, School: '', Remark: '' },
        { Id: 5, Name: '', Age: 22, School: '', Remark: '' }
      ],
      rowtemplate: { Id: 0, Name: '', Age: '', School: '', Remark: '' },
      rowIndex: ''
    };
  },
  methods: {
    Save: function (event) {
      if (this.rowtemplate.Id === 0) {
        // Id
        this.rowtemplate.Id = this.rows.length + 1;
        this.rows.push(this.rowtemplate);
      } else {
        this.rows[this.rowIndex] = this.rowtemplate;
      }
      // 
      this.rowtemplate = { Id: 0, Name: '', Age: '', School: '', Remark: '' };
    },
    DeleteId: function (id) {
      // id
      for (var i = 0; i < this.rows.length; i++) {
        if (this.rows[i].Id === id) {
          this.rows.splice(i, 1);
          break;
        }
      }
    },
    // ididindex
    DeleteIndex (n) {
      this.rows.splice(n, 1);
    },
    Edit: function (row, n) {
      //  this.rowtemplaterow
      // this.rowtemplate = row;
      // 
      this.rowIndex = n;
      this.rowtemplate = JSON.parse(JSON.stringify(row));
    }
  }
};
</script>

<style lang="css" scoped>
  table thead tr th {
    text-align:center;
  }
  table{
    border-collapse: collapse;
    margin-bottom: 20px;
  }
  th,td{
    padding: 5px;
    border: 1px solid #ccc;
  }
</style>



