<template lang="html">
  <div class="">
    <input type="file" name="" value="" @change="uppic">
  </div>
</template>

<script>
import $ from 'jquery';
export default {
  methods: {
    // change
    uppic (e) {
      var file = e.target.files[0]; // [0]
      // 
      if (file.type !== 'image/png') {
        alert('');
        e.target.files.length = 0;
        $(e.target).val('');
        return false;
      }
      // formData
      var formData = new FormData();
      formData.append('pic', $(e.target)[0].files[0]); // 
      formData.append('flag', '1'); // 
      // $.ajax({ // ajax
      //   url: 'xxxxx.php',
      //   type: 'POST',
      //   cache: false,
      //   data: formData,
      //   processData: false,
      //   contentType: false
      //   }).done(function(result) {
      //   console.log('');
      // });
    }
  }
};
</script>

<style lang="css">
</style>



