<template lang="html">
  <div class="vux-marquee" :style="{height: height + 'px'}">
    <ul class="vux-marquee-box" ref="box" :style="{transform: `translate3d(0,${currenTranslateY}px,0)`, transition: `transform ${noAnimate ? 0 : duration}ms`}">
      <slot></slot>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    interval: { // marquee
      type: Number,
      default: 2000
    },
    duration: { // 
      type: Number,
      default: 300
    },
    direction: { // 
      type: String,
      default: 'up'
    }
  },
  data () {
    return {
      currenTranslateY: 0, // y
      height: '', // 
      length: 0, // marquee
      currentIndex: 0, // index
      noAnimate: false // 
    };
  },
  beforeDestroy () {
    this.destroy();
  },
  methods: {
    destroy () {
      this.timer && clearInterval(this.timer);
    },
    // 
    init () {
      this.destroy();
      if (this.cloneNode) {
        this.$refs.box.removeChild(this.cloneNode);
      };
      this.cloneNode = null; // 
      let firstItem = this.$refs.box.firstElementChild; // box
      if (!firstItem) { // li
        return false;
      };
      this.length = this.$refs.box.children.length; // ulli
      this.height = firstItem.offsetHeight;
      // 
      //  up 
      // down,
      if (this.direction === 'up') {
        this.cloneNode = firstItem.cloneNode(true);
        this.$refs.box.appendChild(this.cloneNode);
      } else {
        this.cloneNode = this.$refs.box.lastElementChild.cloneNode(true);
        this.$refs.box.insertBefore(this.cloneNode, firstItem);
      }
    },
    start () {
      if (this.direction === 'down') this.go(false); // 
      this.timer = setInterval(() => {
        console.log(this.currentIndex);
        if (this.direction === 'up') {
          this.currentIndex += 1;
          this.currenTranslateY = -this.currentIndex * this.height;
        } else {
          this.currentIndex -= 1;
          this.currenTranslateY = -(this.currentIndex + 1) * this.height;
        }
        if (this.currentIndex === this.length) {
          setTimeout(() => {
            this.go(true);
          }, this.duration);
        } else if (this.currentIndex === -1) {
          setTimeout(() => {
            this.go(false);
          }, this.duration);
        } else {
          this.noAnimate = false;
        }
      }, this.interval + this.duration);
    },
    go (toFirst) {
      this.noAnimate = true;
      if (toFirst) {
        this.currentIndex = 0;
        this.currenTranslateY = 0;
      } else {
        this.currentIndex = this.length - 1; // marquee-1
        this.currenTranslateY = -(this.currentIndex + 1) * this.height;
        // lidom+1
      }
    }
  }
};
</script>

<style lang="css">
.vux-marquee {
  width: 100%;
  overflow:hidden;
}
.vux-marquee-box {
  padding: 0;
  margin: 0;
  width: 100%;
  height: auto;
}
.vux-marquee-box li {
  margin: 0;
  width: 100%;
  padding:10px 0;
  box-sizing:border-box;
}
</style>



