<template lang="html">
  <div>
    <button @click='a'>1</button>
    <button @click='b'>2</button>
  </div>
</template>

<script>
import Toast from '@/components/toast/toast.js';
export default {
  methods: {
    a () {
      Toast('');
    },
    b () {
      Toast({
        message: '',
        position: 'top',
        duration: 1000
      });
    }
  }
};
</script>

<style lang="css">
</style>



