<template lang="html">
  <div>
    <button @click='demo'></button>
    <p>{{count1}}</p>
    <button @click='demo2'>,</button>
    <p>{{count2}}</p>
  </div>
</template>

<script>
export default {
  data () {
    return {
      count1: 0,
      count2: 0
    };
  },
  created () {
    //  
    this.$on('demo', () => {
      this.count1++;
      if (this.count1 === 5) {
        // 
        this.$off('demo');
      };
    });
    this.$once('demo2', (value) => {
      this.count2 += value;
    });
  },
  methods: {
    demo () {
      // demo 
      this.$emit('demo');
    },
    demo2 () {
      // 
      this.$emit('demo2', 10);
    }
  }
};
</script>

<style lang="css">
</style>



